* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: rgb(63, 63, 63);
  font-family: "Poppins", sans-serif;
  height: auto;
}
.Main_Cntnr {
  background-color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  position: relative;
}
.logobox_Cmpny {
  width: 17%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo {
  margin: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  background-color: #fff;
}
.Info_Accs {
  display: flex;
  justify-content: flex-start;
  margin: 20% 0 0 5%;
  flex-direction: column;
}
.Acc_Info,
.Comp_Info,
.Acc_lvl {
  padding: 6px 35px 6px 6px;
  margin: 4px 0;
}
.Acc_Info:hover,
.Comp_Info:hover,
.Acc_lvl:hover {
  border-radius: 20px;
  border: 1px solid rgba(128, 128, 128, 0.753);
  background-color: #fff;
}
.Acc_Info a,
.Comp_Info a,
.Acc_lvl a {
  text-decoration: none;
  color: #334d6e;
}

.leftbtns_Cmpny {
  margin-top: 100%;
}
.Emailbtn_Cmpny {
  width: 140px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008bdf;
  color: #fff;
  border: none;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}

.Supportbtn_Cmpny {
  width: 140px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192a3e;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}

.lftmail_Cmpny .fa-envelope,
.lftphone_Cmpny .fa-phone-alt {
  padding-right: 8px;
  color: #192a3e;
}
.lftmail_Cmpny a {
  color: #192a3e;
  text-decoration: none;
}
.lftmail_Cmpny,
.lftphone_Cmpny {
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone_Cmpny {
  color: #192a3e;
  text-align: center;
  cursor: pointer;
}

.NavContainerCmpny {
  width: 83%;
  height: auto;
  position: absolute;
  left: 17%;
}
.navbar_Cmpny {
  height: 50px;
  width: 82.5%;
  position: fixed;
  left: 17.1%;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px grey;
  background-color: whitesmoke;
  align-items: center;
  justify-content: flex-end;
}
.list {
  display: flex;
}

.list li {
  margin: 0 30px 0 10px;
  list-style: none;
}
.list li a {
  text-decoration: none;
  color: #192a3e;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,
.list li a .far {
  padding-right: 5px;
  color: #008bdf;
}

.whole {
  width: 100%;
  position: absolute;
  top: 52px;
}
.My_Js_My_As {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  padding-bottom: 2px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.527);
}
.my_job {
  background-color: rgba(128, 128, 128, 0.527);
  color: grey;
  padding-left: 5px;
  display: flex;
  height: 70px;
  align-items: center;
}
.my_job h1,
.my_acc h1 {
  font-size: 1.5em;
  font-weight: 100;
  cursor: pointer;
}
.arrow {
  width: 40px;
  height: 55px;
  font-weight: 100;
  line-height: 70px;
  background-color: #fff;
  border-left: 40px solid rgba(128, 128, 128, 0.527);
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
}
.my_acc {
  margin-left: 20px;
  color: grey;
}
.cmpny_infrmtn {
  color: grey;
  width: 50%;
  margin: 20px;
}
.cmpny_infrmtn h2 {
  line-height: 40px;
  font-weight: 100;
  border-bottom: 1px solid rgba(21, 179, 228, 0.527);
}
.form_c_i {
  width: 80%;
  margin: 20px auto;
}
.form_c_i > div {
  padding: 10px 0;
  font-size: 1.2em;
  display: flex;
  align-items: center;
}
.form_c_i > div label {
  width: 170px;
  text-align: right;
  margin-right: 10px;
}
.form_c_i > div input {
  font-size: 1em;
  border-radius: 5px;
  outline: none;
  width: 350px;
  padding: 3px;
  border: 0.1px solid grey;
}
.file span {
  font-size: 0.8em;
  text-align: center;
}
.form_c_i .phno .phno_ {
  width: 294px;
}
.form_c_i > .file input {
  border: none;
}
.form_c_i .phno .phne3 {
  width: 50px;
  margin-right: 5px;
}
.UCIbtn {
  width: 25%;
  margin: 10px 345px;
}
.UCIbtn button {
  padding: 8px;
  font-weight: bold;
  font-size: 1em;
  color: #fff;
  background-color: #76cf21;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.UCIbtn button:hover {
  color: #76cf21;
  background-color: #fff;
  border: 1px solid #76cf21;
}
